/**
 * 商品详情页的 poi 组件的数据
 * 通过 /rest/tp/item/sku/getItemDetailPoiInfo 接口获取
 */
/**
 * 当前poi 的信息
 */
export interface IPoiData {
    /** 地址信息 */
    address: string;
    /** 区code */
    areaCode: number;
    /** 区 */
    areaName: string;
    /** 平均价格 */
    avgPrice: string;
    /** 一级类目 */
    cate: string;
    /** 市code */
    cityCode: number;
    /** 市 */
    cityName: string;
    /** 距离，单位米 */
    distance: number;
    /** 图片 */
    image: string;
    /** 纬度 */
    latitude: string;
    /** 经度 */
    longitude: string;
    /** poiId */
    poiId: string;
    /** poi名称 */
    poiName: string;
    /** 省code */
    provinceCode: number;
    /** 省 */
    provinceName: string;
    /** 二级类目 */
    subCate: string;
    /** 多少人想去 */
    visitCount: number;
    /** 门店电话 */
    telephones: string[];
}

export interface IProductData {
    /** 供应商客服电话 */
    itemCustomerServiceTel: string;
    /** poi数量 */
    poiSize: number;
}
